import i18n from '@/i18n'

export function umsatzHeaders () {
  const header = [
    {
      text: i18n.t('Name'),
      align: 'start',
      value: 'name',
    },
    {
      text: i18n.t('AllUmsatz'),
      value: 'all',
    },
    // {
    //   text: '100%',
    //   value: 'p100',
    // },
    {
      text: '19%',
      value: 'p19',
    },
    {
      text: '7%',
      value: 'p7',
    },
    {
      text: '0%',
      value: 'p0',
    },

  ]
  const headerNew = header.map(h => {
    return {
      ...h,
      class: 'font-size: 20px',
    }
  })

  return headerNew
}

export function discountHeaders () {
  return [
    {
      text: i18n.t('Table'),
      align: 'start',
      value: 'tableName',
    },
    {
      text: i18n.t('Discount'),
      value: 'discountStr',
    },
    {
      text: i18n.t('servantName'),
      value: 'servantName',
    },
    {
      text: i18n.t('Time'),
      value: 'timeStamp',
    },
    {
      text: i18n.t('DiscountValue'),
      value: 'value',
    },
  ]
}

export function stornHeaders () {
  return [
    { text: i18n.t('Table'), align: 'start', value: 'tableName' },
    { text: i18n.t('Count'), value: 'count' },
    { text: i18n.t('Dishname'), value: 'Dname' },
    { text: i18n.t('returnReason'), value: 'returnReason' },
    { text: i18n.t('Time'), value: 'time' },
    { text: i18n.t('StornValue'), value: 'value' },
    { text: i18n.t('servantName'), value: 'servantName' },
  ]
}
